// components/ChatBox.tsx
import React, { useState } from 'react';

interface Props {
  selectedModel: string;
  apiKey: string;
  persona: string;
  customPrompt: string;
}

export const ChatBox: React.FC<Props> = ({
  selectedModel,
  apiKey,
  persona,
  customPrompt,
}) => {
  const [messages, setMessages] = useState<string[]>([]);
  const [input, setInput] = useState('');

  const handleSend = async () => {
    const userMsg = `üë§ ${input}`;
    setMessages([...messages, userMsg, 'üß† loading...']);

    const res = await fetch('http://localhost:8000/chat', {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        message: input,
        model: selectedModel,
        api_key: apiKey,
        persona,
        custom_prompt: customPrompt,
      }),
    });

    const data = await res.json();
    const updated = [...messages, userMsg, `üß† ${data.response}`];
    setMessages(updated);
    setInput('');
  };

  return (
    <div className="flex flex-col h-full">
      <div className="flex-1 border rounded p-4 overflow-y-scroll bg-white">
        {messages.map((msg, i) => (
          <div key={i} className="mb-2 whitespace-pre-wrap">{msg}</div>
        ))}
      </div>
      <div className="mt-4 flex gap-2">
        <input
          className="flex-1 p-2 border rounded"
          placeholder="Type your message..."
          value={input}
          onChange={(e) => setInput(e.target.value)}
        />
        <button onClick={handleSend} className="bg-green-600 text-white px-4 py-2 rounded">
          Send
        </button>
      </div>
    </div>
  );
};