// components/PersonaSelector.tsx
import React from 'react';

interface Props {
  persona: string;
  setPersona: (val: string) => void;
  customPrompt: string;
  setCustomPrompt: (val: string) => void;
}

export const PersonaSelector: React.FC<Props> = ({
  persona,
  setPersona,
  customPrompt,
  setCustomPrompt,
}) => {
  return (
    <div className="mt-6">
      <label className="block font-semibold">Select Persona</label>
      <select
        className="w-full p-2 border rounded"
        value={persona}
        onChange={(e) => setPersona(e.target.value)}
      >
        <option value="Friendly Assistant">Friendly Assistant</option>
        <option value="Sarcastic Expert">Sarcastic Expert</option>
        <option value="Emotional Mentor">Emotional Mentor</option>
        <option value="Stoic Analyst">Stoic Analyst</option>
      </select>

      <label className="block font-semibold mt-4">Custom Pre-Prompt (Optional)</label>
      <textarea
        className="w-full p-2 border rounded h-24"
        placeholder="You are a supportive life coach..."
        value={customPrompt}
        onChange={(e) => setCustomPrompt(e.target.value)}
      />
    </div>
  );
};